<template>
    <img :src="resolveImageUrl(link)" alt="logo" class="image" loading="lazy" />
</template>

<script>

export default {
    name: 'ImageView',
    components: {
        
    },
    props: {
        link: String,
    },
    methods: {
        resolveImageUrl: function (path) {
                let images = require.context('../assets/images/', false, /\.svg$|\.png$/)
                return images("./"+path)
            } 
    }
}
</script>

<style scoped>
.image{
    width: 200px;
    border: 1px solid rgba(255, 255, 255, 0.322);
    border-radius: 15px;
    box-shadow: 0 8px 16px 0 rgba( 31, 38, 135, 0.37 );
    transition: 300ms ease-in-out;
    margin: 20px;
}

.image:hover{
    border: 1px solid rgba(255, 255, 255, 0.712);
    
}

@media only screen and (max-width: 720px){
    .image{
        min-width: 130px;
        width: 60%;
        box-shadow: 0 4px 8px 0 rgba(31, 38, 135, 0.151);
        margin: 15px;
    }
}
</style>