<template>
  <div id="main-container">
      <img :src="resolveImageUrl(coverName)" alt="cover picture" class="cover">
      <img :src="resolveImageUrl(appPic)" alt="app picture" class="app">
      <div class="title-btn">
        <p class="text name">{{name}}</p>
        <a :href=link class="link" target="_blank"><img src="../assets/icons/github.svg" alt="go to repo"><p>Check it in github</p></a>
      </div>
  </div>
</template>

<script>

export default {
    name: 'GithubView',
    props: {
      coverName: String,
      appPic: String,
      name: String,
      link: String,
    },
    methods: {
        resolveImageUrl: function (path) {
                let images = require.context('../assets/images/', false, /\.svg$|\.png$/)
                return images("./"+path)
            } 
    }
}
</script>

<style scoped>
