  border-radius: 15px;
  border: 2px solid rgba( 255, 255, 255, 0.18 );
  transition: 300ms ease-in-out;
}

#main-container:hover{
  border: 2px solid rgba(255, 255, 255, 0.534);
}

.cover{
  width: 100%;
  object-fit: cover;
  border-top-right-radius: 15px;
  border-top-left-radius: 15px;
  height: 90px;
  z-index: 0;
}

.app{
  margin-top: -40px;
  margin-left: 10px;
  width: 20%;
  border-radius: 50%;
  box-shadow: 0 6px 8px 0 rgba(67, 31, 135, 0.219);
  border: 2px solid rgba(255, 255, 255, 0.493);
  z-index: 1;
  transition: 300ms ease-in-out;
}

.app:hover{
  border: 2px solid rgba(255, 255, 255, 0.842);
}

.title-btn{
  display: inline-flex;
  flex-direction: row;
  align-items: center;
  width: 90%;
  margin-left: 15px;
}

.name{
  margin-right: auto;
}

.link{
  height: 35px;
  padding-right: 10px;
