  align-items: center;
  text-decoration: none;
  color: #FEFEFE;
  font-family: 'Modulus_bold';
  border-radius: 1000px;
  border: 1px solid rgba(255, 255, 255, 0);
  transition: 200ms ease-in-out;
  
}

.link:hover{
  border: 1px solid rgba(255, 255, 255, 0.548);
  background-color: #fefefe48;
}

.link>img{
  width: 30px;
