<template>
  <div id="main-container">
      <img :src="resolveImageUrl(coverName)" alt="cover picture" class="cover">
      <div class="img-btn">
          <img :src="resolveImageUrl(appPic)" alt="app picture" class="app">
          <google-play-button :applink=link :availability=availability />
      </div>
      <div class="title-btn">
        <p class="text name">{{name}}</p>
      </div>
  </div>
</template>

<script>
import GooglePlayButton from './GooglePlayButton.vue'
export default {
    name: 'GooglePlayView',
    components: {
        GooglePlayButton,
    },
    props: {
      coverName: String,
      appPic: String,
      name: String,
      link: String,
      availability: String,
    },
    methods: {
        resolveImageUrl: function (path) {
                let images = require.context('../assets/images/', false, /\.svg$|\.png$/)
                return images("./"+path)
            } 
    }
}
</script>

